@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@800&display=swap');

/*95B1C1 - blue*/
:root {
  --blue: #95B1C1;
  --red: #D15252;
  --green: #556E4E;
  --yellow:  #dcbc52;
}


body {
  background-color: #fcf9ed;
}

h1, h2, h3, h4, .anti, .racism {
  font-family: 'Kanit', sans-serif;
}

.title {
    margin: 50px 10px 10px 10px;
    border-top: 30px solid $red;
    padding-top: 30px;    
}

.midd {
  text-transform: uppercase;
  font-weight: 700;
  color: #000;
  background: $red;
  text-align: center;
}

.anti {
  font-size: 9.3vw;
  line-height: .5;
  display: block;
}
.racism {
  font-size: 6vw;
  line-height: 1;
  display: block;
}

.menu-home-container {
    margin: 50px 10px 10px 10px;
    border-top: 30px solid $blue;
  padding-top: 10px;
}

.menu-home-container a, .etc a {
  font-size: 1.8em;
  font-weight: 700;
  color: black;
}

.menu-home-container ul, .etc ul {
  list-style: none;
  padding-left: 0;
  margin-left: 0;
}

.etc {
    margin: 50px 10px 20px 10px;
    border-top: 30px solid $green;
  padding: 10px 20px;
}

.quote {
  font-size: 1.8em;
  font-family: serif;
  font-style: italic;
  line-height: 1.1;
  margin-top: 10px;
}

.quote-source {
  font-size: 2em;
  line-height: 1.1;
  font-family: 'Kanit', sans-serif;
  margin-top: 15px;
  color: $green;
}

.tag-line {
  background-color: $red;
  padding: 5px;
  margin-bottom: 50px;
}


.label{
  font-family: 'Kanit', sans-serif;
  font-size: 6vw;
  text-align: right;
   margin: 120px 40px 40px 40px;
  position:relative;
  line-height: .6;
  text-transform: uppercase;
}
.label:before{
    position: absolute;
    top: 0%; 
    overflow: hidden;
    width: 100%;
    height: 20px;
    content: '\a0';
    background-color: #000;
}
.label:before {
    margin-left: -100%;
    text-align: right;
}


#events a {
	color: #000;
}

#events a:hover {
	text-decoration: none;
}

a .event {
  border: 6px solid #000;
  margin: 30px;
  text-align: center;
  padding: 20px;
}

a .card {
  border-radius: 0;
  padding: 0;
  background-color: #95B1C1;
}

a .month {
  font-size: 2vw;
}

a .day {
  font-family: 'Kanit', sans-serif;
  font-size: 9vw;
  line-height: .8;
}

a .event-title {
  font-weight: 700;
  font-size: 1.3em;
}

.media {
  margin-top: 50px;
}
.media-box {
  display: flex;
  border-left: 10px solid;
}

.media-type {
  writing-mode: vertical-rl;
  text-orientation: upright;
  font-family: 'Kanit', sans-serif;
  font-size: 4vw;
  letter-spacing: -40px;
/*   padding-bottom: 50px; */
  text-align: center;
  height: 400px;
}

ul li a {
  font-weight: 700;
  color: #000;
  font-size: 1.5em;
}

.nav {
  background-color: #D15252;
  padding: 10px 0;
  margin-bottom: 80px;
}

.nav a {
  text-align: center;
  display: block;
  color: #000;
  font-weight: 700;
  font-size: 1.2em;
  text-transform: uppercase;
  text-decoration: none;
}

.funding-details {
  margin: 10px 30px;
}

img {
  width: 100%;
  height: auto;
}

.green {
   background: hsl(107deg 17% 37%);
   border: $green;
}

.blue {
  background: $blue;
  border: $blue;
}

.red {
  background-color: $red;
  border: $red;
}

.yellow {
  background-color: $yellow;
   border: $yellow;  
}

.green img, .blue img, .red img, .yellow img {
  mix-blend-mode: screen;
  -webkit-filter: grayscale(100%) contrast(200%);
  filter: grayscale(200%) contrast(200%);
  opacity: 1;
   border:  5px solid;
}

.read {
  border-color: $red;
}

.watch {
  border-color: $green;
}

.listen {
  border-color: $blue;
}

.btn-ar {
  display: block;
  width:  270px;
  margin:  60px auto;
  border:  4px solid black;
  border-radius: 0;
  font-size:  1.2em;
  font-weight:  700;
}


.btn-red {
  background-color: $red;
}

.label {
  text-shadow: 3px 3px $blue
}

#people a {
  font-weight: 700;
  color: #000;
  text-align: center;
  display: block;
  font-size: 1.3em;
  margin-top: 5px;
}